<template class='result'>
    <div>
      <van-cell-group>
        <van-field
            v-model="cName"
            required
            clearable
            label=""
            placeholder=""
        />
        <van-field
            v-model="cIdCard"
            label=""
            placeholder=""
            clearable
            required
        />
        <van-field
            v-model="cPhone"
            label=""
            placeholder=""
            clearable
            required
        />
    </van-cell-group>
    <div class="result-buttom-top"></div>
    <van-button type="info" :loading = 'loadingShow' size='large' @click="query()"></van-button>
    </div>
</template>
<script>
import Vue from 'vue'
import { Cell, CellGroup, Image, Field, Button, ActionSheet, Toast, Dialog } from 'vant'
import { axiosPost } from '../../comment/http'
import api from '../../comment/api'
import qs from 'qs'
Vue.use(Cell).use(CellGroup).use(Image).use(Field).use(Button).use(Toast).use(Dialog).use(ActionSheet)
export default {
  name: 'result',
  data () {
    return {
      cIdCard: '',
      cName: '',
      cPhone: '',
      loadingShow: false
    }
  },
  mounted () {
    document.title = ''
  },
  methods: {
    query () {
      if (this.cName === '') {
        this.$toast('')
        return false
      }
      if (this.cIdCard === '' || !(/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(this.cIdCard))) {
        this.$toast('')
        return false
      }
      if (this.cPhone === '' || !(/^1[3456789]\d{9}$/.test(this.cPhone))) {
        this.$toast('')
        return false
      }
      this.loadingShow = true
      axiosPost(`${api.querySignUp}`, qs.stringify({
        cName: this.cName,
        cIdCard: this.cIdCard,
        cPhone: this.cPhone
      }))
        .then((data) => {
          this.loadingShow = false
          if (data.code === 0) {
            this.$router.push({name: 'Result', params: data})
          } else {
            this.$toast(data.msg)
            Dialog.alert({
              title: '',
              message: data.msg
            })
          }
        })
    }
  }
}
</script>
<style lang="scss" scoped src="./index.scss">
</style>



